import { AfterViewInit, ChangeDetectorRef, Component, EventEmitter, Inject, Injector, Input, OnDestroy, OnInit, Optional, Output, ViewChild } from '@angular/core';
import * as _ from 'lodash-es';
import { Subscription } from 'rxjs';
import { noDataImg } from '../../assets/resource';
import { ApiService } from '../../utils/api.service';
import { CommonUtil } from '../../utils/common.tool';
import { FavoritesTool } from '../../utils/favorites.tool';
import { ChangeType, Group, GroupItem } from '../../utils/index.type';
import { StateMgrTool } from '../../utils/state.mgr.tool';
import { tap, switchMap } from 'rxjs/operators';
import { AppendGroupOrgComponent } from '../append-group-org/appendorg.component';
import { FormMessageService, FormLoadingService } from '@farris/command-services';

@Component({
    selector: 'lib-group',
    templateUrl: './group.component.html',
    styleUrls: ['./group.component.css']
})
export class GroupComponent implements OnInit, AfterViewInit, OnDestroy {

    @ViewChild('appendOrg') appendOrg: AppendGroupOrgComponent;
    @Input() primaryField = 'orgId';
    @Input() multiSelect: any;
    @Input() absOrgType: string;
    @Input() busNum: string;
    @Input() enableFavorites: boolean;
    @Input() notGetStopOrg: boolean;
    @Input() groupApiUrl: string;
    @Input() enableHierarchicalLoading: boolean;
    @Input() cascadeCheck: boolean;
    @Input() cascadeDown: boolean;
    @Input() cascadeUp: boolean;
    @Input() keepSelect: boolean;
    @Input() layer: number;
    @Input() selectedLimit: number;
    @Input() showCheckAll: boolean; 
    @Output() groupChange = new EventEmitter<any>();

    groupIdField = 'id';
    userIdField = 'userId';
    selections: any[] = [];
    noDataImg = noDataImg;
    allChecked = false;
    items = [];
    favoritesList = [];
    groupData: Group = { public: [], personal: [] };
    personnelList = [];
    groupPersonnels = [];
    publicVisible = true;
    personalVisible = true;
    publicToken = 'public';
    personalToken = 'personal';
    selectedGroupId: string;
    allIndexId: string;
    showGroupOrgDialog = false;
    currentGroupType = 'public';
    private selectionsSub: Subscription;
    private favoritesSub: Subscription;

    constructor(
        private apiService: ApiService,
        private favoritesTool: FavoritesTool,
        private stateMgr: StateMgrTool,
        private cdr: ChangeDetectorRef,
        private injector: Injector,
        private formMessageService: FormMessageService,
        private formLoadingService: FormLoadingService
    ) {
        this.allIndexId = CommonUtil.newGuid();
    }

    ngOnDestroy(): void {
        if (this.enableFavorites) {
            this.favoritesSub.unsubscribe();
        }
        this.selectionsSub.unsubscribe();
    }

    ngOnInit(): void {
        if (this.enableFavorites) {
            this.favoritesSub = this.favoritesTool.favoritesSubject.subscribe(data => {
                this.favoritesList = data;
                this.setFavorites();
            });
        }
        this.selectionsSub = this.stateMgr.selectionsSubject.subscribe(data => {
            if (data.changeType === ChangeType.ValueChanged) {
                this.selections = _.cloneDeep(data.value);
                this.toggleChecked();
            } else if (data.changeType === ChangeType.Remove) {
                this.unSelectLine(data.value);
            } else if (data.changeType === ChangeType.Append) {
                this.selectLine(data.value);
            }
            this.cdr.detectChanges();
        });
        this.getGroupList();
    }

    ngAfterViewInit(): void {

    }

    assembleData() {
        this.getGroupList();
    }

    // 变更群组内的组织
    updateGroup() {
        const addList: any[] = [];
        const primaryIds = this.items.map(item => { return item[this.primaryField]}).join();
        this.appendOrg.currentOrgs.forEach(org => {
            if(primaryIds.indexOf(org[this.primaryField]) === -1) {
                addList.push(org[this.primaryField]);
            }
        });
        const addOrgsInCurrentGroup$ = this.apiService.saveGroup({
            groupId: this.selectedGroupId,
            addIds: addList,
            deleteIds: [],
            changeType: Number(3)
        });
        addOrgsInCurrentGroup$.pipe(
            tap(
                (data) => {
                    if(data) {
                        console.log('已为当前群组追加新的组织成员！');
                        this.getOrgsByGroup({ id: this.selectedGroupId }, this.currentGroupType);
                        this.toggleGroupOrgDialog(false);
                    }
                }
            )
        ).subscribe();
    }

    // 删除群组
    deleteGroup(e: MouseEvent, item: GroupItem) {
        this.formMessageService.confirm("是否删除此群组？").pipe(
            switchMap((result: boolean) => {
                if(!!result) {
                    if (item[this.groupIdField] === this.selectedGroupId) {
                        this.selectedGroupId = null;
                        this.currentGroupType = this.publicToken;
                    }
                    const deleteGroup$ = this.apiService.saveGroup({
                        groupId: item[this.groupIdField],
                        changeType: Number(4)
                    });
                    return deleteGroup$.pipe(
                        tap(
                            (data) => {
                                if(data) {
                                    console.log('群组删除成功！');
                                    this.getGroupList();
                                    e.stopPropagation();
                                    e.cancelBubble = true;
                                }
                            }
                        )
                    )
                } else {
                    return;
                }
            })
        ).subscribe();
    }

    // 变更群组基本信息（名称、描述）
    editGroup(e: MouseEvent, item: GroupItem) {
        this.selectedGroupId = item[this.groupIdField];
        this.groupChange.next(item);
        e.stopPropagation();
        e.cancelBubble = true;
    }

    // 删除群组内的组织
    removeOrg(e: any, data: any) {
        this.formMessageService.confirm("是否将此组织移出当前群组？").pipe(
            switchMap((result: boolean) => {
                if(!!result) {
                    const deleteOrg$ = this.apiService.saveGroup({
                        groupId: this.selectedGroupId,
                        addIds: [],
                        deleteIds: [data[this.primaryField]],
                        changeType: Number(3)
                    });
                    return deleteOrg$.pipe(
                        tap(
                            (data) => {
                                if(data) {
                                    console.log('群组内组织删除成功！');
                                    this.getOrgsByGroup({ id: this.selectedGroupId }, this.currentGroupType);
                                    e.stopPropagation();
                                    e.cancelBubble = true;
                                }
                            }
                        )
                    )
                } else {
                    return;
                }
            })
        ).subscribe();   
    }

    // 设置群组内组织的勾选状态
    private setChecked(data: any, checked: boolean) {
        const itemIndex = CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.items);
        if (itemIndex > -1) {
            this.items[itemIndex].checked = checked;
        }
    }

    // 设置群组内组织的收藏状态
    private setFavorites() {
        this.items.forEach(item => {
            if (CommonUtil.findIndex(item[this.primaryField], this.primaryField, this.favoritesList) !== -1) {
                item.inFavorites = true;
            } else {
                item.inFavorites = false;
            }
        })
    }

    // 通过弹窗增加群组内的组织
    toggleGroupOrgDialog(show: boolean, groupType = this.personalToken) {
        if (groupType === this.personalToken) {
            this.showGroupOrgDialog = show;
        }
    }

    // 获取某个群组内的所有组织
    getOrgsByGroup(item: any, groupType = this.publicToken) {
        this.formLoadingService.show();
        this.currentGroupType = groupType;
        this.allChecked = false;
        this.selectedGroupId = item[this.groupIdField];
        // const helpExtend = window && (window as any).orgHelpExtend;
        // helpExtend.getDataByGroupId(item[this.groupIdField], this.absOrgType).then(data => {

        // });
        this.apiService.getGroupMembers(item[this.groupIdField], this.absOrgType).subscribe((data: any) => {
            this.items = data ? data : [];
            this.groupData.personal.forEach(item => {
                if (item[this.groupIdField] === this.selectedGroupId && item.total !== this.items.length) {
                    item.total = this.items.length;
                }
            });
            this.items.forEach(it => {
                if (CommonUtil.findIndex(it[this.primaryField], this.primaryField, this.favoritesList) !== -1) {
                    it.inFavorites = true;
                } else {
                    it.inFavorites = false;
                }
                it.indexId = Math.random().toString(16).substring(2);
                // it.displayName = !!this.showUserCode ? ('[' + it.code + ']' + it.name) : it.name;
            });
            this.toggleChecked();
            this.setFavorites();
            this.formLoadingService.hide();
        });
    }

    // 获取组织群组列表
    getGroupList() {
        this.apiService.getOrgGroups(this.absOrgType).subscribe((data: any) => {
            const resPublic = data && data.publicGroup;
            const resPersonal = data && data.privateGroup;
            this.groupData.public = resPublic ? resPublic : [];
            this.groupData.personal = resPersonal ? resPersonal : [];
            const firstPublicGroup = resPublic[0] && resPublic[0][this.groupIdField];
            const firstPersonalGroup = resPersonal[0] && resPersonal[0][this.groupIdField];
            this.selectedGroupId = this.selectedGroupId ? this.selectedGroupId : firstPublicGroup;
            this.selectedGroupId = this.selectedGroupId ? this.selectedGroupId : firstPersonalGroup;
            if (!!this.selectedGroupId) {
                if (this.groupData.public.findIndex(item => item[this.groupIdField] === this.selectedGroupId) !== -1) {
                    this.currentGroupType = this.publicToken;
                } else if (this.groupData.personal.findIndex(item => item[this.groupIdField] === this.selectedGroupId) !== -1) {
                    this.currentGroupType = this.personalToken;
                }
                this.getOrgsByGroup({ id: this.selectedGroupId }, this.currentGroupType);
            }
        })
    }

    // 收藏或者取消收藏组织
    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    // 切换公共群组或者个人群组的收折状态
    toggleVisible(e, type: string) {
        if (type === this.publicToken) {
            this.publicVisible = !this.publicVisible;
        } else if (type === this.personalToken) {
            this.personalVisible = !this.personalVisible;
        }
        e.stopPropagation();
        e.cancelBubble = true;
    }

    toggleAll(e: any) {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
        e.preventDefault();
    }

    toggleOne(data, e) {
        if (data.checked) {
            if (this.multiSelect) {
                this.emitSelections(data, ChangeType.Remove);
            } else {
                this.emitSelections([]);
            }
        } else {
            if (this.multiSelect) {
                this.emitSelections(data, ChangeType.Append);
            } else {
                this.emitSelections(new Array(data));
            }
        }
        e.preventDefault();
    }

    /**
     * 全选后处理
     * @param dataArr 全选的值
     */
    checkAll() {
        this.items.forEach(item => {
            if (CommonUtil.findIndex(item[this.primaryField], this.primaryField, this.selections) === -1) {
                this.emitSelections(item, ChangeType.Append);
            }
        });
    }

    /**
     * 全部取消
     * @param dataArr 取消的数据
     * @returns void
     */
    unCheckAll() {
        const checkeds = this.items.filter(item => item.checked);
        checkeds.forEach(item => {
            this.emitSelections(item, ChangeType.Remove);
        })
    }

    /**
     * 当前行选中
     * @param data 当前行的数据（）
     */
    selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.selections = [];
            this.items.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        if (this.isAllChecked()) {
            this.allChecked = true;
        }
    }

    /**
     * 单选取消选中
     * @param data 取消选中行
     */
    unSelectLine(data: any) {
        const index = CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        this.setChecked(data, false);
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private emitSelections(selections: any, changeType = ChangeType.ValueChanged) {
        this.stateMgr.emitSelections(selections, changeType);
    }

    /**
     * 选中并插入当前行数据到右侧
     * @param data 当前行的数据
     * @param collection 暂存当前数据的集合
     */
    _insertOneIntoSelections(data: any) {
        // 如果当前数据已经移入右侧数据，则返回。
        if (CommonUtil.findIndex(data[this.primaryField], this.primaryField, this.selections) > -1) {
            return;
        }
        this.setChecked(data, true);
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 翻页检查：人员展示区的勾选状态与已选人员区是否对应
     */
    toggleChecked() {
        this.items.forEach(item => {
            if (CommonUtil.findIndex(item[this.primaryField], this.primaryField, this.selections) > -1) {
                item.checked = true;
            } else {
                item.checked = false;
            }
        });
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private isAllChecked(): boolean {
        let count = 0;
        let allChecked = false;

        this.items.forEach(item => {
            this.selections.forEach(selection => {
                if (item[this.primaryField] === selection[this.primaryField]) {
                    count += 1;
                }
            });
        });
        if (this.items.length === count && count > 0) {
            allChecked = true;
        }
        return allChecked;
    }

    trackByFn(index, item) {
        return index;
    }

}
