import {
    Component, Input, EventEmitter, Output, OnInit, AfterViewInit, SimpleChanges, OnChanges, ChangeDetectorRef, Injector, NgZone, OnDestroy
} from '@angular/core';
import { ApiService } from '../../utils/api.service';
import * as _ from 'lodash-es';
import { FavoritesTool } from '../../utils/favorites.tool';
import { ChangeType, OrgRequestParam } from '../../utils/index.type';
import { CommonUtil } from '../../utils/common.tool';
import { StateMgrTool } from '../../utils/state.mgr.tool';
import { Subscription } from 'rxjs';

@Component({
    selector: 'lib-selection',
    templateUrl: './sl.component.html',
    styleUrls: ['./sl.component.css'],
})
export class SelectionComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {

    @Input() primaryField: string;
    @Input() enableGroup: boolean;
    @Output() openedChange = new EventEmitter<boolean>();

    datas = [];
    items = [];
    pageIndex = 0;
    pageSize = 5;
    moreText = '没有更多数据啦~';
    selectionsSub: Subscription;

    constructor(
        private apiService: ApiService,
        private cdr: ChangeDetectorRef,
        private favoritesTool: FavoritesTool,
        private injector: Injector,
        private ngZone: NgZone,
        private stateMgr: StateMgrTool
    ) {
        this.cdr = this.injector.get(ChangeDetectorRef);
    }

    ngOnDestroy(): void {
        this.selectionsSub.unsubscribe();
    }

    ngOnInit(): void {
        this.selectionsSub = this.stateMgr.selectionsSubject.subscribe(data => {
            this.pageIndex = 0;
            if (data.changeType === ChangeType.ValueChanged) {
                this.datas = _.cloneDeep(data.value);
            } else if (data.changeType === ChangeType.Remove) {
                this.datas = this.datas.filter(item => item[this.primaryField] !== data.value[this.primaryField]);
            } else if (data.changeType === ChangeType.Append) {
                if (CommonUtil.findIndex(data.value[this.primaryField], this.primaryField, this.datas) === -1) {
                    this.datas.push(data.value);
                }
            }
            this.items = this.datas.slice(this.pageIndex, this.pageSize);
            if (this.items.length < this.datas.length) {
                this.moreText = '更多';
            } else {
                this.moreText = '没有更多数据啦~';
            }
            this.cdr.detectChanges();
        })
    }

    ngAfterViewInit(): void {

    }

    ngOnChanges(changes: SimpleChanges): void {

    }

    openGroupDialog() {
        this.openedChange.emit(true);
    }

    trackByFn(index, item) {
        return index;
    }

    loadMore() {
        if (this.pageIndex < this.datas.length) {
            this.pageIndex += 1;
            const currentPageIndex = this.pageIndex * this.pageSize;
            const currentItems = this.datas.slice(currentPageIndex, currentPageIndex + this.pageSize)
            this.items = this.items.concat(currentItems);
            if (this.items.length && this.items.length === this.datas.length) {
                this.moreText = '没有更多数据啦~';
            }
            this.cdr.detectChanges;
        }
    }

    removeItem(e: any, data: any, i: any) {
        this.stateMgr.emitSelections(data, ChangeType.Remove);
        if(i !== 0) {
            e.stopPropagation();
            e.cancelBubble = true;
        }
    }


}
